				function(success) {
					
					console.log(uni.getStorageSync('itemOne').objectId);
				},
				function(error) {
					// 删除失败
				}
			); */
		}
	},
	watch: {
		second: {
			handler(newVal) {
				this.num(newVal);
			}
		},
		minute: {
			handler(newVal) {
				this.num(newVal);
			}
		}
	},
	mounted() {
		this.add();
	},
	computed: {
		second: function() {
			return this.num(this.seconds);
		},
		minute: function() {
			return this.num(this.minutes);
		}
	},
	methods: {
		num: function(n) {
			return n < 10 ? '0' + n : '' + n;
		},
		add: function() {
			var _this = this;
			var time = setInterval(function() {
				if (_this.seconds === 0 && _this.minutes !== 0) {
					_this.seconds = 59;
					_this.minutes -= 1;
				} else if (_this.minutes === 0 && _this.seconds === 0) {
					_this.seconds = 0;
					clearInterval(time);
					_this.outTime = true
				} else {
					_this.seconds -= 1;
				}
			}, 1000);
		},
